<template>
  <div id="app">
    <div>
      <select @change="nameChange('selectedProd',$event)">
        <option value="">--请选择--</option>
        <option :value ="item" v-for="item in prodNames" :key="item">{{item}}</option>
      </select>
      <select @change="nameChange('selecteColor',$event)">
        <option value="">--请选择--</option>
        <option :value ="item" v-for="item in colors" :key="item">{{item}}</option>
      </select>
      <select @change="nameChange('selectedSpec',$event)">
        <option value="">--请选择--</option>
        <option :value ="item" v-for="item in specs" :key="item">{{item}}</option>
      </select>
      <button @click="addProd">添加</button>
      <button @click="filterProd">结算</button>
    </div>
   
    <table class="product-table" cellpadding="0" cellspacing="0">
      <tr class="border-bt"><td colspan="8" class="title">爱墅美彩铝落水系统价格表</td></tr>
      <tr class="border-bt">
        <th>序号</th>
        <th>产品名称</th>
        <th>单位</th>
        <th>数量</th>
        <th>单价</th>
        <th>金额</th>
        <th>颜色</th>
        <th>规格</th>
      </tr>
      <tr class="border-bt" v-for="(item,index) in tableData" :key="item.id">
        <td>{{index + 1}}</td>
        <td>{{item.prodName}}</td>
        <td>{{item.unit}}</td>
        <td>
          <input type="number" placeholder="请输入数量" :value="item.account" class="my-input" @input="handleChange(index,$event)" >
        </td>
        <td>{{item.price}}</td>
        <td>{{(item.account * (+item.price)).toFixed(2)}}</td>
        <td>{{item.color}}</td>
        <td>{{item.spec}}</td>
      </tr>
      <tr class="border-bt"><td colspan="8" class="total">总价：{{totalPrice.toFixed(2)}}元（不含税票）</td></tr>

    </table>
  </div>
</template>

<script>
import responseData from './data'

export default {
  name: 'App',
  data() {
    return {
      selectedSpec: "",
      selectedColor: "",
      selectedProd: "",
      specs: ["5寸","6寸","7寸","8寸","型材"],
      colors: ["白色","灰色","米黄","咖啡色"],
      prodNames: ["檐沟","吊件","连接器","阳角","阴角","下水器","封盖","雨水管","45度弯头","90度弯头"],
      tableData: [],
      productList: responseData.data
    }
  },
  computed: {
    totalPrice() {
      var total = 0
      this.tableData.forEach(val => {
        total += val.account * val.price
      })
      return total
    }
  },
  methods: {
    handleChange(index, e) {
      var obj = this.tableData[index]
      var val = e.target.value 
      if (val) {
        obj.account = +val
      } else {
        obj.account = val
      }
      this.$set(this.tableData, index, obj)
    },
    nameChange(name, e) {
      this[name] = e.target.value
    },
    filterProd(){
      this.tableData = this.tableData.filter(val => !!val.account)
    },
    addProd() {
      if (!this.selectedColor && !this.selectedProd && !this.selectedSpec) {
        alert('请添加过滤条件！')
        return ;
      }
      var list = this.productList.filter(prod => {
        var flag1 = this.selectedProd ? (prod.prodName === this.selectedProd) : true
        var flag2 = this.selectedSpec ? (prod.spec === this.selectedSpec) : true
        var flag3 = this.selectedProd ? (prod.color === this.selectedProd) : true
        return flag1 && flag2 && flag3
      })
      if (list.length === 0) {
        alert('无匹配项')
      } else {
        list.forEach(val => {
          var flag = false
          val.account = 0
          var item
          for (var i = 0; i < this.tableData.length; i++) {
            item = this.tableData[i]
            if (item.id === val.id) {
              flag = true
              break
            }
          }
          if (!flag) {
            this.tableData.push(val)
          }
        })
      }
    }
  }
}
</script>

